.makeFlex {
  display: flex;
}
p, body, ul {
  margin: 0;
  padding: 0;
}

.makeFlex.column {
  flex-direction: column;
}

.bold {
  font-weight: 900;
}

.blackText {
  color: #000;
}

.blueText {
  color: darkBlue;
}

.makeFlex.perfectCenter {
  align-items: center;
  justify-content: center;
}

.makeFlex.vrtlCenter {
  align-items: center;
}

.makeFlex.hrtlCenter {
  justify-content: center;
}

.font10 {
  font-size: 10px;
}

.font14 {
  font-size: 14px;
}

.textLeft {
  text-align: left;
}

.body.bodyFixed {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  overflow: hidden;
}

.changeLanguageLink {
  position: absolute;
  top: 80px;
  right: 50px;
}

.changeLanguageLink a {
  padding: 12px 20px;
  background: #008cff;
  border-radius: 96px;
  color: #fff;
  font-size: 14px;
  font-weight: 900;
  box-shadow: 0px 0px 5px 0px rgba(0,0,0,0.5);
  text-transform: uppercase;
  text-decoration: none;
}

@media only screen and (max-width: 480px) {
  .changeLanguageLink {
    top: 50px;
    left: 50%;
    right: unset;
    transform: translateX(-50%);
  }

  .changeLanguageLink a {
    padding: 0;
    background: transparent;
    border-radius: none;
    color: #008cff;
    box-shadow: none;
  }
}
